import PropTypes from 'prop-types'
import Trigger from 'rc-trigger'
import 'rc-trigger/assets/index.css' // @todo 
import React, { Component } from 'react'

function getPopupAlign(align) {
  return {
    offset: [align.offsetX, align.offsetY],
    overflow: {
      adjustX: 1,
      adjustY: 1,
    },
  }
}

/**
 *     .
 * */
class Positional extends Component {
  render() {
    const {
      align,
      builtinPlacements,
      children,
      destroyPopupOnHide,
      events,
      placement,
      popupComponent,
      stretch,
      transitionName,
    } = this.props
    return (
      <Trigger
        destroyPopupOnHide={destroyPopupOnHide}
        popup={popupComponent}
        popupAlign={getPopupAlign(align)}
        popupPlacement={placement}
        popupTransitionName={transitionName}
        action={events}
        builtinPlacements={builtinPlacements}
        stretch={stretch}
      >
        {children}
      </Trigger>
    )
  }
}

Positional.propTypes = {
  /**       */
  align: PropTypes.shape({
    offsetX: PropTypes.number,
    offsetY: PropTypes.number,
  }),
  /**       */
  builtinPlacements: PropTypes.object,
  /** ,       */
  children: PropTypes.element,
  /**       */
  destroyPopupOnHide: PropTypes.bool,
  /** ,   .   - click, contextMenu, focus, hover */
  events: PropTypes.arrayOf(PropTypes.string),
  /**     ( ) */
  mouseEnterDelay: PropTypes.number,
  /**     ( ) */
  mouseLeaveDelay: PropTypes.number,
  /**     .      builtinPlacements */
  placement: PropTypes.oneOf(['left', 'right', 'top', 'bottom', 'topLeft', 'topRight', 'bottomLeft', 'bottomRight']),
  /**    */
  popupComponent: PropTypes.element,
  /**        .  - 'width', 'minWidth', 'height', 'minHeight'   , . 'height minWidth' */
  stretch: PropTypes.string,
  /**    . , rc-trigger-popup-zoom */
  transitionName: PropTypes.string,
}

Positional.defaultProps = {
  align: {
    offsetX: 0,
    offsetY: 0,
  },
  builtinPlacements: {
    left: {
      points: ['cr', 'cl'],
    },
    right: {
      points: ['cl', 'cr'],
    },
    top: {
      points: ['bc', 'tc'],
    },
    bottom: {
      points: ['tc', 'bc'],
    },
    topLeft: {
      points: ['bl', 'tl'],
    },
    topRight: {
      points: ['br', 'tr'],
    },
    bottomRight: {
      points: ['tr', 'br'],
    },
    bottomLeft: {
      points: ['tl', 'bl'],
    },
  },
  destroyPopupOnHide: false,
  events: ['click', 'hover'],
  mouseEnterDelay: 0,
  mouseLeaveDelay: 0.1,
  placement: 'left',
  popupComponent: <h5>I'm default popup element!</h5>,
  stretch: null,
  transitionName: null,
}

Positional.displayName = 'Positional'

/** @component */
export default Positional
