import styled from 'styled-components'
import PropTypes from 'prop-types'
import { alignItems, justifyContent, flexWrap, flexDirection } from 'styled-system'
import Box from './Box'
import theme from '../theme'

/**
 *   `<Box />`,   **display: flex**    **alignItems, justifyContent, flexWrap, flexDirection**.
 */
const Flex = styled(Box)`
  display: flex;

  ${alignItems} 
  ${justifyContent} 
  ${flexWrap} 
  ${flexDirection};
`

Flex.propTypes = {
  /**  - */
  alignItems: PropTypes.oneOf(['flex-start', 'flex-end', 'center', 'baseline', 'stretch']),
  /**
   *  -: One of: flex-start, flex-end, center, space-between, space-around, space-evenly.
   *     . ,   : ['flex-start', 'flex-end', 'center']
   * */
  justifyContent: PropTypes.oneOfType([
    PropTypes.oneOf(['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly']),
    PropTypes.array,
  ]),
  /**
   *    flex-        ,   .
   *   ,    ,    
   * */
  flexWrap: PropTypes.oneOf(['nowrap', 'wrap', 'wrap-reverse']),
  /**
   *              
   */
  flexDirection: PropTypes.oneOf(['row', 'row-reverse', 'column', 'column-reverse']),
  /** @ignore */
  theme: PropTypes.any,
}

Flex.defaultProps = {
  justifyContent: 'flex-start',
  alignItems: 'stretch',
  flexDirection: 'row',
  flexWrap: 'nowrap',
  theme,
}

Flex.displayName = 'Flex'

/** @component */
export default Flex
